.whiteBlock {
  border-radius: 2rem;
  overflow-y: auto;
  padding: 3rem 4rem;
  width: 100%;
  max-width: 42rem;
  min-height: 25rem;
  .closeWrapper {
    top: 2.4rem;
    right: 3.2rem;
    height: fit-content;
  }
  h2 {
    font-size: 3.6rem;
  }
  p, label, b {
    font-size: 1.6rem;
  }
  input {
    font-size: 1.8rem;
  }


}
